import { State } from ".";

export function getImageSrc(filename: string) {
  if (!filename) {
    return null;
  }
  return require(`@/assets/images/${filename}`);
}

export function getPilotNumberImg(state: State, id: string, year: number) {
  const pilot = state.pilots.find((pilot) => pilot.id === id);
  if (!pilot) {
    return null;
  }
  // if year undefind set it at current date
  if (year === undefined) {
    year = new Date().getFullYear();
  }
  // find pilot.visuals.numbers.image where number.sinceDate is matching with year or before
  const filename = pilot.visuals.numbers.find(
    (number) => number.sinceYear <= year
  )?.image;

  if (filename === undefined) {
    return null;
  }
  return getImageSrc(filename);
}

export function getTeamCurrentBrand(state: State, id: string) {
  const team = state.teams.find((team) => team.id === id);
  if (!team || !team.brand.length) {
    return null;
  }
  return team.brand[0];
}

export function getManufacturer(state: State, id: string) {
  return state.manufacturers.find((manufacturer) => manufacturer.id === id);
}

export function getManufacturerVisuals(state: State, id: string, year: number) {
  const manufacturer = getManufacturer(state, id);
  if (!manufacturer) {
    return null;
  }
  // if year undefind set it at current date
  if (year === undefined) {
    year = new Date().getFullYear();
  }
  // find manufacturer.visuals where sinceYear is matching with year or before
  return manufacturer.visuals.find((visual) => visual.sinceYear <= year);
}
