import { createStore } from "vuex";
import Manufacturers from "@/data/manufacturers.json";
import Teams from "@/data/teams.json";
import Seasons from "@/data/seasons.json";
import Pilots from "@/data/pilots.json";
import {
  getImageSrc,
  getManufacturer,
  getManufacturerVisuals,
  getPilotNumberImg,
  getTeamCurrentBrand,
} from "@/store/getters";

export interface State {
  manufacturers: typeof Manufacturers;
  teams: typeof Teams;
  pilots: typeof Pilots;
  seasons: typeof Seasons;
}

export default createStore({
  state: {
    manufacturers: Manufacturers,
    teams: Teams,
    pilots: Pilots,
    seasons: Seasons,
  },
  getters: {
    imageSrc: () => (filename: string) => getImageSrc(filename),
    pilotNumberImg: (state) => (id: string, year: number) =>
      getPilotNumberImg(state, id, year),
    teamCurrentBrand: (state) => (id: string) => getTeamCurrentBrand(state, id),
    manufacturer: (state) => (id: string) => getManufacturer(state, id),
    manufacturerVisuals: (state) => (id: string, year: number) =>
      getManufacturerVisuals(state, id, year),
  },
  mutations: {},
  actions: {},
  modules: {},
});
