<template>
  <div>
    <h1>See the Timeline {{ msg }}</h1>
  </div>
  <ul class="alphabet">
    <li v-for="team in teams" :key="team.id">
      <div :style="{ backgroundColor: team.color }">
        <span
          >{{ team.label }} ({{ team.manufacturer }}
          <img class="manu-logo" :src="team.manufacturerLogo" />)</span
        >
        <img class="team-logo" :src="team.logo" />
      </div>
    </li>
    <li v-for="pilot in pilots" :key="pilot.id">
      <span>{{ pilot.firstName }} - {{ pilot.nickName }} !</span>
      <img class="pilot-number" :src="pilot.numberImage" />
    </li>
  </ul>
</template>

<script lang="ts">
import { Vue } from "vue-class-component";
import { useStore } from "vuex";

// const store = useStore();

export default class Timeline extends Vue {
  msg = "Hello, world!";
  teams = useStore().state.teams.map((team: any) => {
    const brand = useStore().getters.teamCurrentBrand(team.id);
    console.log("brand", brand);
    const manufacturer = useStore().getters.manufacturer(brand.manufacturerId);
    console.log("manufacturer", manufacturer);
    const logoImageSrc = useStore().getters.imageSrc(brand.visuals?.logo);
    const manufacturerVisuals = useStore().getters.manufacturerVisuals(
      brand.manufacturerId
    );
    const manufacturerLogoImageSrc = useStore().getters.imageSrc(
      manufacturerVisuals?.logo
    );

    return {
      id: team.id,
      label: brand.label,
      logo: logoImageSrc,
      color: brand.visuals?.primaryColor,
      manufacturer: manufacturer.label,
      manufacturerLogo: manufacturerLogoImageSrc,
    };
  });
  pilots = useStore().state.pilots.map((pilot: any) => {
    return {
      id: pilot.id,
      firstName: pilot.firstName,
      nickName: pilot.nickName,
      numberImage: useStore().getters.pilotNumberImg(pilot.id),
    };
  });
}
</script>

<style scoped>
.pilot-number {
  margin: 2px;
  object-fit: contain;
  object-position: center;
  height: 2em;
  width: 4em;
}
.team-logo {
  margin: 2px;
  object-fit: contain;
  object-position: center;
  height: 2em;
}
.manu-logo {
  margin: 2px;
